.body {
    background-image: url(/src/Assets/Images/Login_wallpaper.png);
    background-repeat: no-repeat;
    background-size: cover;
    justify-content: center;
}

.login-box-component {
    display: flex;
    width: 600px;
    padding: 52px 60px 40px 60px;
    flex-direction: column;
    align-items: center;
    gap: 36px;
    border-radius: 10px;
    background: var(--tc-colors-type-tc-white, #FFF);
    /* Shadows */
    box-shadow: 0px 6px 24px 0px rgba(0, 0, 0, 0.10);

}